// font
.front-color-dark {
  color: $active-font-dark;
}
.front-color-light {
  color: $active-font-light;
}
.front-color-grey {
  color: $active-font-grey;
}

p {
  font-size: $font-size-p;
  line-height: 1.5 * $font-size-p;
  text-indent: 2 * $font-size-p;
  margin: 0;
  padding: $font-size-p $font-size-p/2;
}

// navigation
.nav {
  position: fixed;
  z-index: 999;
  width: 100%;
  background-color: $active-font-dark;
  color: #fff;
}
.nav-top {
  top: 0;
  height: 50px;
}
.nav-bottom {
  bottom: 0;
  height: 60px;
}

// main
body {
  color: $active-font-grey;
}
